import { FontColor } from 'core/types/common';

import { getColorNameByAlias } from '../helpers';

import styles from './styles.scss';

const useFontColor = (color?: FontColor | 'inherit'): string | undefined => {
  const basicColor = getColorNameByAlias(color);

  if (basicColor === 'inherit') {
    return undefined;
  }

  const className = styles[`color_${ basicColor }`];

  if (!className) {
    console.error(`Unsupported color value received: ${ color }`);
  }

  return className;
};

export default useFontColor;
